          isOpen: false,
          question: '畢業學生學習心得',
          answer: `<span style="font-weight: bold;">畢業學生 & 助教 Wendy：</span><br>
          <a class="special-a" href="https://ithelp.ithome.com.tw/m/articles/10317577" target="_blank">學生寫的學習心得 1</a>、<a class="special-a" href="https://ithelp.ithome.com.tw/m/articles/10325081" target="_blank">學生寫的學習心得 2</a><br><br>
          <span style="font-weight: bold;">畢業學生 & 助教 Yolanda Chang：</span><br>
          找上西打的時候，我已經先自學程式好一段時間，也從面試經驗中得到一些回饋，說實在有公司願意讓我這樣的轉職小白去面試真的很值得感恩，那時我甚至還沒在履歷中放任何作品。面試官在看完我的小作品後，建議我運用學到的技能寫一個完整的專案，在做功能較複雜專案的過程中也會學習到，如何解決單獨寫小功能時沒想過的問題，也因此開啟了我做專案的旅程。<br><br>
          我畢業於服裝設計系，對設計美感類的事情本身就很有興趣，在出社會的這幾年中身邊同樣設計背景的同學有好幾個陸續跳到資訊產業，必須很現實地說主因還是台灣的薪資結構。他們選擇的多為 UIUX 設計，我一開始也是先接觸這塊，慢慢摸索後發現自己對前端工程更有興趣，加上疫情期間工作型態的轉換，讓我開始思考自己的未來，也開始在下班後接觸程式。<br><br>
          在自學及找工作的過程中，不免也有十分迷惘的時候，尤其在做專案初期，光是想辦法將以前輸入的內容輸出成肉眼可見的成品，就令我一個頭兩個大。曾經想過要不要放棄自學，申請 AppWorks School 課程，沒有順利得到面試機會。還是去報名產業新尖兵計畫？趁 30 歲前都還有政府補助，在看完課綱後還是猶豫了，課綱中的內容我都已經自學過，我需要的只是培訓班後段的專案指導、以及就業媒合，真的要投入半年再去聽一次課嗎？<br><br>
          自學的路上相對孤獨，幸運活在資訊時代的我在 instagram 上追蹤了許多前端軟體工程師的帳號，在學習閒暇時看看大家分享技術與學習記錄，比起看到平時朋友們分享各種出去玩、工作的日常，沒正職工作、整天閉關自學的我會覺得相對比較沒壓力，也會覺得滑手機時沒那麼廢(?)。<br><br>
          也是這個契機下我看見 Wendy 分享她找西打當 mentor 的故事，發現比起轉職培訓班，我需要的其實是這個吧，難道這就是宇宙的力量嗎！當下就私訊詢問西打是否有再接新學生的打算，西打也十分迅速的回應我所有提出的問題，「可以直接跳過學習的過程開始做專案，做完專案安排模擬面試」，也表示我現在手上完成度一半的專案，剛好可以當課程中的第二專案接續完成。我彷彿在轉職的路上看見一線曙光，沒思考太久就決定報名了。<br><br>
          西打教學的方式我認為滿像之前設計系在做畢業專題，時程進度自己規劃，不定時找助教與指導老師核對方向。第一專案以臨摹西打設計的網站為主，順便熟悉業界的git流程，第二專案的內容與要用的技術由自己決定，跟西打討論後他會依據自身經驗與學生打算就業的方向提供建議，可以參考看看是否要在方向上稍做修正。<br><br>
          至於為什麼最初會選擇自學、以及建議擁有怎樣特質的人考慮自學，我好像也可以簡單提供我的想法。<br><br>
          首先，在求學過程中的經驗是，我會在課堂中因為沒聽懂一個觀念而卡住無法吸收後面的課程內容，是靠下課後自己一條一條梳理過每條算式、觀念與邏輯，確實理解吸收後，才體會到將所學觀念運用自如解題的快樂。所以我確定我對想學習的事情不需靠人催促，自己就能從理解中獲取成就感，並且靠這些繼續走下去；反而在無法自行運用時間，慢下來去釐清卡住的地方時，對我來說是虛耗的光陰。<br><br>
          第二，培訓班學習方式是每日 8~12 小時從早到晚，培訓班說明會也表明無法兼職、須全職學習，而我希望可以一邊兼職家教一邊學習，一方面是支撐待業期間開銷、一方面也是想在生活中有心態上的轉換，在學習一天後，強迫自己出門跟學生聊聊天，也在學習與指導的角色中做轉換，讓自己可以用不同的角度看待學習過程中遇到的問題。<br><br>
          當然我都只是分享自身經驗，如果你想清楚了，就算跟我的經驗與特質天差地遠，還是可以選擇找西打當 mentor 用這個接近自學的模式學習。畢竟軟體工程師這條路上，就是不斷有新科技新技術、不斷推陳出新，自學是遲早必須具備的技能。而且每個來參與課程的人應該都會跟西打與課程群組的大家擦出不一樣的火花，感覺會滿有趣的。

          `,
        },
      ]
